.picu-welcome {
	position: relative;
	padding-top: 15px;
	box-sizing: border-box;
	max-width: 1024px;
	margin: 0 auto;

	@include clear-after;

	*, *:before, *:after {
		box-sizing: inherit;
	}

	.picu-welcome-subtitle {
		padding: 0; // empty h2 to fix admin notices on welcome screen
	}

	.picu-logo {
		display: block;
		margin: 2rem auto 2rem auto;
		width: 120px;
		height: auto;

		@media only screen and (min-width: 800px) {
			width: 180px;
		}
	}

	img {
		max-width: 100%;
		height: auto;
	}

	.row {
		margin: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		@media only screen and (min-width: 800px) {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
		}

		&.header-row {
			margin-bottom: 2rem;
		}

		&.row-white {
			@media only screen and (min-width: 800px) {
				align-items: stretch;
				margin: 0 -1rem 1.5rem -1rem;
			}
		}
	}

	.column {
		position: relative;

		@media only screen and (min-width: 800px) {
			padding: 0 1rem;

			&.col-33 {
				width: calc( 33.3333% - 2rem );
			}

			&.col-50 {
				width: 50%;
			}
		}
	} // .column

	.header-row {

		.column:first-child {
			padding: 0 2rem 0 0;
			margin-bottom: 2rem;

			@media only screen and (min-width: 800px) {
				margin-bottom: 0;
			}

			h1 {
				margin: 0 0 0.5rem 0;
				padding: 0;
				font-size: 30px;
				font-weight: 700;
			}

			p {
				margin: 0 0 1rem 0;
				font-size: 16px;

				&.thanks {
					font-size: 20px;
					color: #999;
					font-weight: 700;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.column:nth-child(2) {
			padding: 0 1rem;

			@media only screen and (min-width: 800px) {
				margin-bottom: 0;
				padding: 0;
				text-align: right;

				img {
					max-width: 440px;
				}
			}

			img {
				box-shadow: 0 0 10px 0 #bbb;
				border-radius: 3px 3px 0 0;
			}
		}
	} // .row-big

	.row-white {

		.column {
			position: relative;
			margin: 1rem;
			padding: 0.5rem 1.5rem 1rem;
			background-color: white;

			@media only screen and (min-width: 800px) {
				margin-bottom: 0;
			}
		}

		h2 {
			margin-bottom: 1rem;
			font-size: 20px;
		}

		hr {
			margin: 1.25rem 0;
			border: none;
			border-bottom: 1px solid #eee;
		}

		hr + h2 {
			margin-top: 0;
		}

		.get-started {
			p {
				margin: 0 0 1rem 0;

				&.alt {
					margin-bottom: 2rem;
				}
			}
		}

		.picu-pro-meta-box {
			margin: 1rem 1rem 0;
			width: 100%;

			@media only screen and (min-width: 800px) {
				width: calc( 33.3333% - 2rem );
			}

			h2 {
				margin-bottom: 0;
			}
		}
	}
} // .picu-welcome