/*
 * Hide stuff, we don't need for picu cpts
 */
.post-status-delivered,
.post-status-approved,
.post-status-sent,
.post-status-expired {

	#edit-slug-box {
		display: none;
	}

	#wp-content-editor-tools {
		display: none;
	}

	#wp-content-wrap {
		display: none;
	}

	#titlediv {
		display: none;
	}

	#postdivrich {
		display: none;
	}

	.wrap {
		& > h2 {
			display: none;
		}
	}

}

.post-status-delivery-draft {
	.page-title-action {
		display: none !important;
	}
}

#post-body #normal-sortables {
	min-height: 0;
}


/*
 * Styling image upload and share options
 */
.post-type-picu_collection {

	#edit-slug-box {
		min-height: 12px;
		margin-bottom: 20px;
	}

	#poststuff #post-body-content {

		h2 {
			margin: 0;
			padding: 8px 12px;
			font-size: 14px;
			line-height: 1.4;
			font-weight: 600;
			background-color: white;
			border-bottom: 1px solid #ccd0d4;
			@include clear-after;

			.stepcounter {
				display: block;
				float: left;
				margin: 0 6px 0 0;
				padding: 4px 0 0 0;
				color: white;
				background-color: #0073aa;
				width: 20px;
				height: 20px;
				border-radius: 50%;
				font-size: 12px;
				line-height: 1;
				text-align: center;
			}
		}

		.picu-postbox-header {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			padding: 8px 12px;
			width: 100%;
			font-size: 14px;
			line-height: 1.4;
			font-weight: 600;
			background-color: white;
			border-bottom: 1px solid #ccd0d4;

			h2 {
				padding: 0;
				border: none;
			}

			.button {
				margin-bottom: 0;
				font-weight: 400;
			}
		}
	}

	.picu-postbox {
		box-sizing: border-box !important;

		*, *:before, *:after {
			box-sizing: border-box !important;
		}
	}

	.picu-postbox-inner {
		// Postioning hack so that the uploader area is not full screen
		position: static !important;

		.uploader-window {
			position: absolute;
		}

	}

	.picu-max-file-size {
		font-size: 12px;
		color: rgba(0, 0, 0, 0.66);
	}
}

.picu-help {
	position: relative;
	display: inline-block;
	margin: -2px 0 0;
	background-color: #ccc;
	background-color: rgba(0, 0, 0, 0.5);
	width: 16px;
	height: 16px;
	font-style: normal;
	text-indent: -10000em;
	border-radius: 50%;

	.picu-max-file-size & {
		margin: -2px 3px 0;
	}

	&:hover {
		background-color: #00a0d2;
	}

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 16px;
		height: 16px;
		text-indent: 0;
		background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 24 24' fill='none' stroke='%23ffffff' stroke-width='3' stroke-linecap='square' stroke-linejoin='arcs'%3e%3ccircle cx='12' cy='12' r='10' stroke='transparent'%3e%3c/circle%3e%3cpath d='M9.09 9a3 3 0 0 1 5.83 1c0 2-3 3-3 3'%3e%3c/path%3e%3cline x1='12' y1='17' x2='12' y2='17'%3e%3c/line%3e%3c/svg%3e") no-repeat center center;
	}
}

.picu-sort-options-wrapper {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
	padding: 5px 5px 0;

	.picu-gallery-has-images & {
		display: block;
	}
}

select.picu-sort-options__select {
	padding: 0 24px 0 8px;
	min-height: 26px;
	max-height: 26px;
	font-size: 11px;
	line-height: 2.18181818;
}

.wp-core-ui .button.picu-sort-options__button {
	margin: 0;
	padding: 0 8px;
	min-height: 0;
	font-size: 11px;
	line-height: 2.18181818;
}

.picu-gallery-thumbnails {
	display: none;
	padding: 12px;
	overflow: auto;
	box-sizing: border-box;
	user-select: none;
	max-height: auto;
	border-bottom: 1px solid #eee;

	&:empty {
		padding: 0;
	}

	.js-collapsed & {
		overflow: hidden;
		height: 200px;
	}

	figure {
		width: 50%;
		margin: 0;
		float: left;
		line-height: 0;
		padding: 8px;
		box-sizing: border-box;

		@media only screen and (min-width: 500px) {
			width: 33.33333%;
		}

		@media only screen and (min-width: 740px) {
			width: 25%;
		}

		@media only screen and (min-width: 1000px) {
			width: 20%;
		}

		@media only screen and (min-width: 1250px) {
			width: 16.66666%;
		}

		@media only screen and (min-width: 1500px) {
			width: 14.2857143%;
		}

		@media only screen and (min-width: 2000px) {
			width: 12.5%;
		}

		.picu-gallery-thumbnail-box {
			padding: 5px;
			border: 1px solid $border-line;
		}

		.picu-gallery-thumbnail-box-inner {
			position: relative;
			width: 100%;

			&:before {
				content: '';
				display: block;
				padding-top: 100%;
			}
		}

		img {
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto;
			max-width: 100%;
			height: auto;
			max-height: 100%;
			width: auto;
		}
	}

	.picu-gallery-has-images & {
		display: block;
	}
}

.post-status-approved .picu-gallery-thumbnails {
	display: none;
}

.picu-delivery-postbox .picu-gallery-thumbnails {
	border-bottom: none;
}

.picu-delivery-postbox.picu-gallery-has-images .picu-gallery-thumbnails {
	border-bottom: 1px solid #eee;
}

.picu-theft-protection-watermark-toggle {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 10px 20px;
	background-color: #f5f5f5;
	border-bottom: 1px solid #eee;
	line-height: 1;

	input[type=checkbox] {
		margin: 0 5px 0 0;
	}

	label {
		margin: 0 10px 0 0;
	}

	&.is-disabled label {
		opacity: 0.5;
	}
}

.picu-watermark-saving-indicator {
	padding-right: 16px;

	&.is-saving,
	&.is-saved {
		padding-right: 0;
	}

	.picu-watermark-saving {
		display: none;
		margin: 0;
		padding: 0;
		width: 16px;
		height: 16px;
		text-indent: -10000em;
		background: url(../images/spinner-2x.gif) no-repeat right center;
		background-size: 16px 16px;
		opacity: 0.5;
	}

	&.is-saving .picu-watermark-saving {
		display: block;
	}

	.picu-watermark-saved {
		display: none;
		margin: 0;
		padding: 0;
		width: 16px;
		height: 16px;
		text-indent: -10000em;
		background: url(../images/icon-check-grey.svg) no-repeat right center;
		background-size: 16px 16px;
		opacity: 0.5;
		animation: fadeOut 2s forwards;
		animation-delay: 2s;
	}

	&.is-saved .picu-watermark-saved {
		display: block;
	}
}


.picu-folder-import {
	position: relative;
	padding: 25px 20px 23px;
	background-color: #f5f5f5;
    border-bottom: 1px solid #eee;
	text-align: center;

	.picu-delivery-postbox & {
		background-color: white;

		.picu-import-progress-bar {
			background-color: #f5f5f5;
			border: 1px solid #f5f5f5;
		}
	}

	.picu-delivery-postbox.picu-gallery-has-images & {
		background-color: #f5f5f5;

		.picu-import-progress-bar {
			background-color: white;
			border: 1px solid white;
		}
	}

	.or {
		position: absolute;
		left: 0;
		bottom: -10px;
		width: 100%;

		span {
			display: inline-block;
			padding: 3px 5px 0 5px;
			height: 19px;
			font-size: 11px;
			line-height: 1;
			text-align: center;
			text-transform: uppercase;
			color: white;
			background-color: #0073aa;
			// background-color: #666;
			border-radius: 3px;
		}
	}

	#picu-import-folder {
		margin-top: 0px;
		max-width: 150px;
	}

	#picu-import-images-form-folder {
		vertical-align: top;
	}

	p {
		margin: 0;
	}
	.picu-import-status {
		display: none;
	}
	.picu-importing {
		float: left;
		margin-bottom: 10px;
	}
	.picu-percentage {
		float: right;
		margin-bottom: 10px;
	}
	.picu-import-progress-bar {
		clear:both;
		width: 100%;
		height: 10px;
		background-color: white;
		border: 1px solid white;

		div {
			width: 0px;
			height: 8px;
			background-color: #0073aa;
		}
	}
	.picu-temporary-import {

		p:nth-of-type(1) {
			margin: 0 0 7px;
		}
		p:nth-of-type(2) {
			margin: 0 0 10px;
		}
	}
}

.picu-toolbar {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin: 0 0 0rem 0;
	padding: 0.5em 1rem;
	background-color: white;
	border-top: 1px solid#ccd0d4;
	border-bottom: 1px solid #ccd0d4;

	@media only screen and (min-width: 784px) {
		justify-content: space-between;
	}
}

.picu-view-switch {
	float: left;
	margin-right: 1rem;

	a {
		display: block;
		margin: 0;
		width: 30px;
		height: 30px;
		float: left;
		background: white;
		text-indent: -1000em;
	}
	.grid-view {
		background: url("../images/grid.png") no-repeat center center;
		background-size: 30px 30px;
		border-right: none;
		border-radius: 2px 0 0 2px;

		&.active {
			background: url("../images/grid-active.png") no-repeat center center;
			background-size: 30px 30px;
			box-shadow: inset 0 0 1px 1px transparentize( black, 0.8 );
		}
	}
	.list-view {
		background: url("../images/list.png") no-repeat center center;
		background-size: 30px 30px;
		border-radius: 0 2px 2px 0;

		&.active {
			background: url("../images/list-active.png") no-repeat center center;
			background-size: 30px 30px;
			box-shadow: inset 0 0 1px 1px transparentize( black, 0.8 );
		}
	}
}

.picu-filter {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	gap: 5px;
	margin: 0.5em 0.5em 0.5em 0;
	padding: 0 0 0 0;
	@include clear-after;

	@media only screen and (min-width: 784px) {
		justify-content: center;
	}

	label {
		margin: 1px 0 0 0;
		font-size: 10px;
		font-weight: bold;
		line-height: 1;
		text-transform: uppercase;
		letter-spacing: 1px;
		color: #777;
	}

	select {
		margin: 0;
		max-width: calc(100% - 3rem);
		max-width: 240px;
	}
}

.picu-download-proof-file .button.disabled {
	pointer-events: none;
	cursor: default;
}

.picu-copy {
	margin: 0.5em 0;

	.button {
		margin-bottom: 0;
	}
}

.picu-copy-to-clipboard {
	position: relative;

	&.picu-copied-success {
		padding-right: 30px;

		&:after {
			content: '';
			position: absolute;
			top: 12px;
			right: 10px;
			width: 15px;
			height: 15px;
			background: url(../images/icon_check.svg) no-repeat center center;
			background-size: 16px 12px;

			@media only screen and (min-width: 784px) {
				top: 7px;
				background-size: 14px 10px;
			}
		}
	}
}

.wp-core-ui .button.picu-copy-to-clipboard {
	margin-bottom: 0;
}

label[for=picu-copy-filenames], #picu-copy-filenames {
	display: none;
}

.picu-table-wrap {
	max-width: 100%;
	overflow-x: auto;
}

.picu-selection-overview-table {
	clear: both;
	width: 100%;
	margin: 0;
	padding: 0;
	border-collapse: collapse;
	font-size: 1em;
	line-height: 1.4;

	thead {
		th {
			padding: 1rem 1rem;
			text-align: left;
			vertical-align: center;

			&.approved {
				width: 80px;
			}

			&.thumbnail {
				width: 50px;
			}

			&.file {
				text-align: left;
			}
		}
	}

	tbody {
		tr {
			border-top: 1px solid #eee;
		}
		// xy
		td {
			padding: 0.5rem 1rem;
		}
	}

	td.approved {
		align-items: center;
		text-align: center;
		vertical-align: middle;
	}

	.selected td.approved .approved-marker {
		display: inline-block;
		margin: 0 auto;
		width: 20px;
		height: 15px;
		background: url('../images/selected.svg') no-repeat center center;
		background-size: 20px 15px;
		text-indent: -1000em;
	}

	td.approved .selection-count {
		display: inline-block;
		margin: 0 auto;
		color: $success-color;
		font-size: 1.5em;
		line-height: 1;
	}

	.thumb {
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 70px;
			height: 70px;
		}

		img {
			max-width: 100%;
			max-height: 100%;
			width: auto;
			height: auto;
		}
	}

	.comments {
		padding-top: 1rem;
		padding-bottom: 1rem;
	}

	.picu-comment {
		margin: 0 0 1rem;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	.picu-comment__inner {
		position: relative;
		margin: 0;
		padding: 0.5rem 1.75rem 0.5rem 1rem;
		background-color: #f5f5f5;
		border-radius: 10px;
	}

	.picu-comment__meta {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 0.25rem 1rem 0;
		
		@media only screen and (min-width: 1120px) {
			flex-direction: row;
		}
	}

	.picu-comment__author {
		font-size: 0.75em;
		font-weight: 700;
		max-width: 150px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.picu-comment__time {
		font-size: 0.75em;
	}

	.picu-comment-marker {
		display: block;
		position: absolute;
		top: .65rem;
		right: .65rem;
		width: 13px;
		height: 13px;
		border: 2px solid $info-color;
		border-radius: 50%;

		&:before {
			content: '';
			position: absolute;
			left: 20%;
			top: 20%;
			width: 60%;
			height: 60%;
			background: $info-color;
			border-radius: 50%;
		}
	}

	.no-match {
		text-align: center;
	}
}

.picu-stars {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	gap: 1em;

	.picu-stars__per-user {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	.picu-stars__wrap {
		display: flex;
		flex-wrap: nowrap;
	}

	.picu-stars__star_icon {
		display: inline-block;
		width: 16px;
		height: 16px;

		svg {
			margin: 0;
			padding: 0;
			width: 16px;
			height: 16px;
			fill: #2271b1;
		}
	}

	.picu-stars__meta {
		padding: 0.25em 0 0 0.1em;
	}

	.picu-stars__author {
		display: block;
		font-size: 0.75em;
		font-weight: 700;
		max-width: 150px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

}

.picu-gallery-uploader {
	margin: 0;
	padding: 20px;
	background-color: white;
	border: none;

	p {
		text-align: center;
	}
}

.post-status-auto-draft .picu-gallery-uploader {
	border: 2px dashed #eee;
	margin: 12px;
	padding: 2em;
}

.picu-gallery-has-images .picu-gallery-uploader {
	margin: 0;
	padding: 20px 20px 10px;
	background-color: #f5f5f5;
	border: none;
}

.toggle-picu-gallery-height {
	position: relative;
	display: none;
	width: 100%;
	padding: 10px 20px;
	border-bottom: 1px solid #eee;
	background-color: #f5f5f5;
	font-size: 0.875em;
}

.is-collapsible {
	.toggle-picu-gallery-height {
		display: block;
		text-align: center;

		.hide {
			display: none;
		}

		&.active {
			.show {
				display: none;
			}
			.hide {
				display: inline;
			}
		}
	}
}

.picu-selection-notice {
	padding: 1rem 1rem 0 1rem;
	width: 100%;
	text-align: center;
	color: $text;

	p {
		margin: 0;
	}
}

.picu-drag-info {
	display: none;
	font-size: 12px;
	color: rgba(0, 0, 0, 0.66);
}

.post-status-auto-draft .picu-postbox .picu-drag-info {
	display: block;
}

.post-status-auto-draft .picu-postbox.picu-gallery-has-images .picu-drag-info {
	display: none;
}

.media-modal.picu-upload-modal .media-modal-content {

	// Hide left menu
	.media-frame-menu {
		display: none;
	}

	.media-frame-menu-toggle {
		display: none !important;
	}

	// Move title to the left
	.media-frame-title {
		left: 0;
	}

	// Hide "Actions" h2 
	.media-frame-menu-heading {
		display: none;
	}

	// Move main modal content to the left
	.media-frame-content {
		left: 0;
	}

	// Hide right sidebar
	.media-sidebar {
		display: none;
	}

	.attachments-browser {

		// We want the attachment/upload area to fill the whole modal
		.attachments-wrapper {
			top: 0;
			right: 0;
		}

		.attachment {
			.describe {
				display: none;
			}
		}

		// Hide upper toolbar (year-filter, reverse sort etc.)
		.media-toolbar {
			display: none;
		}

		.uploader-inline {
			right: 0;
		}
	}

	.media-frame-toolbar {
		left: 0;
	}
}

// Fix media library assistant re-arrangement
.post-type-picu_collection .media-modal .media-toolbar {
	right: 0;
}

.picu-share-options {
	margin-bottom: 20px;
	box-sizing: border-box;
	background-color: white;
	border: 1px solid #ccd0d4;
	box-shadow: 0 1px 1px rgba(0,0,0,.04);
	@include clear-after;

	h2 {
		@include clear-after;
	}

	* {
		box-sizing: inherit;
	}
	p {
		padding: 0 20px;
		@include clear-after;
	}

	label {
		display: block;
		margin: 0 0 5px 0;
		width: 100%;
		font-weight: 600;
	}

	input[type="checkbox"] + label, input[type="radio"] + label {
		display: inline-block;
		width: auto;
	}

	input[type="text"], input[type="email"], textarea {
		margin: 0;
		width: 100%;
	}

	textarea {
		max-height: 160px;
	}

	.picu-share-option {
		display: none;

		&.is-active {
			display: block;
		}
	}

	#picu-collection-email-address,
	#picu-delivery-email-address {
		display: none;
	}

	.picu-share-option-email-field {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;	
		width: 100%;
		margin-bottom: 5px;
		padding: 4px 4px 0;
		box-shadow: 0 0 0 transparent;
		border-radius: 4px;
		border: 1px solid #7e8993;
		background-color: #fff;
		color: #32373c;
		font-size: 14px;
		line-height: 2;
		min-height: 30px;

		&:focus {
			border-color: #007cba;
			box-shadow: 0 0 0 1px #007cba;
			outline: 2px solid transparent;
		}
	}

	.email-address {
		display: inline-block;
		margin: 0 4px 4px 0;
		padding: 3px 6px 5px;
		color: white;
		background-color: #007cba;
		border-radius: 2px;
		line-height: 1;

		&:first-child {
			margin-left: 0;
		}

		&:last-child {
			margin-right: 6px;
		}
	}

	.delete-email-address {
		display: inline-block;
		margin: 0 0 0 6px;
		width: 13px;
		height: 13px;
