#picu-submit-metabox {

	.inside {
		margin: 0;
		padding: 0;
	}

	.save-draft {
		border: none;
		box-shadow: none;
		padding: 0;
		float: left;
		color: $info-color;
		background: transparent;

		&:hover {
			background: inherit;
			color: scale-color( $info-color, $lightness: 20% );
		}
	}

	.spinner {
		float: left;
		transform: translateY(2px);
	}

	.disabled + .spinner {
		visibility: visible;
	}

	#major-publishing-actions {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		gap: 10px;
		padding: 10px 12px 0px;
	}

	.post-status-auto-draft & #major-publishing-actions,
	.post-status-draft & #major-publishing-actions,
	.post-status-delivery-draft & #major-publishing-actions {
		justify-content: space-between;
	}
}

.picu-submit-metabox-inside {
	position: relative;
}

.status {
	position: relative;
	display: flex;
	align-items: center;
	margin: 1em;
	font-size: 1.2em;
	font-weight: 700;

	&:before {
		content: '';
		display: inline-block;
		margin-right: 0.4em;
		width: 11px;
		height: 11px;
		border: none;
		border-radius: 50%;
		background-color: currentColor;
	}

	&.status--draft {
		color: $draft-color;
	}

	&.status--open {
		color: $primary;
	}

	&.status--closed {
		color: $closed-color;
	}

	&.status--delivered {
		color: $delivered-color;
	}
}

.status__meta {
	margin-top: 0;
	padding: 0 12px 0.5em;
}

.status__meta-table {
	margin: 0;
	padding: 0;
	width: 100%;
	font-size: 0.875em;
	border-radius: 3px;
	border-spacing: 0;

	th {
		padding: 0.5em 0.5em 0.5em 0;
		text-align: left;
		border-bottom: 1px solid #eee;
	}

	td {
		padding: 0.5em 0 0.5em 0.5em;
		text-align: right;
		border-bottom: 1px solid #eee;
	}

	tr:last-child th,
	tr:last-child td {
		border-bottom: none;
	}

	.status__meta-table__expiry.status__meta-table__expiry--soon {
		color: $alert-color;
	}
}

.picu-post-options {
	background: #f5f5f5;
	background: white;
	font-size: 0.875em;
	border-top: 1px solid #eee;

	&:empty {
		border: none;
	}

	.picu-option-item {
		position: relative;
		display: block;
		padding: 10px 12px 8px 12px;
		border-bottom: 1px solid #eee;

		&:last-child {
			margin: 0;
			border-bottom: none;
		}
	}

	.picu-password-wrap {
		position: relative;
	}

	#picu-password {
		margin: 5px 0;

		&.is-hidden {
			margin: 0;
			height: 0;
			overflow: hidden;

			.picu-remove-password {
				display: none;
			}
		}

		label {
			display: block;
		}
		input[type="text"] {
			margin: 0 0 3px 0;
			width: 100%;
		}

		.picu-remove-password {
			position: absolute;
			bottom: 10px;
			right: 25px;
			width: 10px;
			height: 10px;
			background: white url(/wp-admin/images/xit-2x.gif) no-repeat left top;
			background-size: 20px 10px;
			border: 1px solid #c2c2c2;
			border-radius: 50px;
			text-indent: -10000em;

			&:hover {
				background: white url(/wp-admin/images/xit-2x.gif) no-repeat right top;
				background-size: 20px 10px;
				border: 1px solid #f29393;
			}

			&.is-hidden {
				display: none;
			}
		}

		.picu-hint {
			margin: 0;

			&.is-hidden {
				display: none;
			}
		}
	}

	a {
		display: inline-block;
		text-decoration: none;

		.dashicons {
			font-size: 15px;
		}
		.dashicons-lock {
			font-size: 16px;
		}
	}

	.picu-copy-collection-url {
		display: flex;
		padding-top: 14px;
		padding-bottom: 14px;

		.picu-collection-url {
			margin: 0;
			width: 100%;
			max-width: calc(100% - 46px);
			border-radius: 4px 0 0 4px;
			border-right: 0;
		}

		.picu-copy-collection-url-to-clipboard {
			margin: 0;
			width: 46px;
			min-height: 30px;
			font-size: 14px;
			color: white;
			background: #2271b1;
			border: 1px solid #2271b1;
			border-radius: 0 4px 4px 0;
			box-shadow: 0 0 0 transparent;

			&:before {
				content: "\f481";
				text-indent: 0;
			}

			@media screen and (max-width: 782px) {
				min-height: 40px;
			}
		}

		.picu-tooltip-copied-url {
			position: absolute;
			z-index: 1000000;
			top: 46px;
			right: 8px;
			padding: .5em .75em;
			max-width: calc(100% - 19px);
			font-size: 11px;
			line-height: 1.5;
			text-align: center;
			word-wrap: break-word;
			color: white;
			background: black;
			border-radius: 6px;
			display: none;

			&:before {
				position: absolute;
				z-index: 1000001;
				top: -6px;
				right: 22px;
				width: 0; 
				height: 0; 
				border-left: 6px solid transparent; 
				border-right: 6px solid transparent;
				border-bottom: 6px solid black;
				border-top: none;
				color: black;
				content: '';
			}

			@media screen and (max-width: 782px) {
				top: 56px;
			}
		}
	}

	#collection_expires {
		float: left;
		margin: 1px 6px 10px 0;
	}

	label[for="collection_expires"] {
		color: #2271b1;
	}

	.expiration-date__wrap {
		display: none;

		label {
			display: block;
			margin-bottom: 0px;
		}
	}

	#collection_expires:checked ~ .expiration-date__wrap {
		display: block;
		margin: 10px -12px -8px -12px;
		padding: 0px 12px 14px 12px;
	}
}

.picu-modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: transparentize( black, 0.25 );
	z-index: 100000; // <- Because, WordPress

	&.is-hidden {
		display: none;
	}

	.picu-modal-inner {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;
		height: 100%;
	}

	.picu-modal-content {
		min-width: 300px;
		max-width: 320px;
		background-color: white;
		box-shadow: 0 0 20px transparentize( black, 0.75 );

		h3 {
			padding: 15px 15px 0;
			margin: 0 0 1em 0;
		}
		p {
			clear: both;
			padding: 0 15px;
			margin: 0 0 1em 0;
		}

		.picu-modal-actions {
			@include clear-after;
			margin-top: 15px;
			padding: 15px;
			background-color: #f5f5f5;
			border-top: 1px solid #ddd;
		}

		.picu-modal-actions > .picu-cancel-modal {
			float: right;
		}
	}

	&.picu-warning .picu-modal-content {
			border-top: 3px solid $alert-color;
	}
} // . picu-modal


#major-publishing-actions {
	padding: 10px 12px;
	border-top: 1px solid #ddd;

	.picu-duplicate-collection-selected {
		margin-top: 8px;
	}
}

.picu-show-approval-message,
.picu-show-collection-description {
	text-align: center;
	width: 100%;

	a {
		&:focus {
			box-shadow: none;
		}
	}

}

.icon-lock {
	&:before {
		content: '\f160';
		font-family: Dashicons;
		padding-right: 0.4em;
	}

	color: #9e9e9e;
	width: 100%;
	text-align: center;
}

.picu-submit-metabox-status-approved .picu-submit-metabox-status-text {
	display: inline-block;
	margin: 0 auto;
	padding-left: 24px;
	background: url(../images/selected.svg) no-repeat left 10px;
	background-size: 20px 15px;
}

.picu-submit-metabox-status {

	text-align: center;
	padding: 1.2em 0 2em;

	div {
		display: inline-block;
	}
}

.picu-submit-metabox-status-text {
	width: auto;
	padding: 0.4em 0.8em 0 0.9em;
	font-size: 1.6em;

	.picu-submit-metabox-status-approved & {
		color: $primary;
	}
}

.re-open-collection {
	text-align: center;
	width: 100%;
	padding-bottom: 1em;
}

#major-publishing-actions {
	.picu-delete {
		padding: 5px 0 10px;
		width: 100%;
		color: $closed-color;
		text-align: center;
	}
}


/**
 * picu options meta box
 */
#picu-options-metabox {
	border: none;
	background: transparent;
	box-shadow: none;

	h2 {
		display: none;
	}

	.handlediv {
		display: none;
	}

	.inside {
		margin: 0;
		padding: 0;
	}

	.picu-post-options {
		font-size: 0.875em;
		background: transparent;
		border-top: none;

		a {
			display: block;
			margin-bottom: 5px;
			text-decoration: none;
		}

		a .dashicons {
			font-size: 15px;
		}
	}

	#major-publishing-actions {
		border-top: none;
	}
}


/**
 * PRO meta box
 */
#picu-pro-metabox {
	border: none;
	box-shadow: none;
	border-radius: 5px;

	.handlediv,
	.hndle {
		display: none;
	}

	.inside {
		margin: 0;
		padding: 0;
	}
}

/**
 * picu Pro metabox
 */
#picu-pro-metabox .picu-pro-meta-box,
.picu-pro-meta-box {
	position: relative;
	margin-top: 0;
	padding: 25px 0 20px 0;
	color: white;
	background-color: var(--picu-admin--primary--color);
	border: none;
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(0,0,0,0.2);

	.picu-pro-meta-box__title {
		margin: 0 0 15px;
		padding: 0 25px;
		font-size: 1.1rem;
		line-height: 1.4;
		font-weight: 700;
		text-align: center;
		color: white;
	}

	.picu-pro-meta-box__content {
		ul {
			padding: 0 20px 0 40px;
			list-style: square;
		}
	}

	.picu-pro-meta-box__reviews {
		margin: 0 20px 10px;
		text-align: center;

		> p:first-child {
			margin: 15px 20px 5px;
		}

		> p:nth-child(2) {
			margin: 0 20px 2px;
		}
	}

	.picu-pro-meta-box__rating {
		display: inline-block;
		margin: 0 0 0 5px;
		transform: translateY(-3px);
		font-weight: 700;
	}

	.picu-pro-meta-box__button-wrap {
		margin: 0;
		padding: 0.9rem 1rem 0.5rem;
		text-align: center;

		.button.picu-pro__button {
			padding: 0.25rem 1.5rem;
			max-width: 100%;
			font-weight: bold;
			color: var(--picu-admin--primary--color);
			background-color: white;
			white-space: normal;
			box-shadow: 0 0 5px rgba(0,0,0,0.15);
		}
	}

	.picu-pro-meta-box__link {
		color: white;
	}
}

// Hide meta box sortables for picu collections
#picu-submit-metabox .handle-actions .handle-order-higher,
#picu-submit-metabox .handle-actions .handle-order-lower,
#picu-pro-metabox .postbox-header {
	display: none;
}

#picu-collection-history .postbox {
	padding: 0;
	background-color: yellow;
}

#picu-collection-history .inside {
	margin: 0;
	padding: 0;
	max-height: 300px;
	overflow-y: auto;
}

.picu-event {
	padding: 0.5em 1em;
}

.picu-event:nth-child(odd) {
	background-color: #f6f7f7;
}

.picu-event__time {
	font-size: 10px;
	display: block;
}

.picu-event__name {
	display: block;
	font-weight: 700;
}

.picu-event__data {
	display: block;
	font-size: 10px;
}
